import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

/**/
const state = {
    infoLoading: true, //,true,false
    baseUrl: 'http://develop.jianguolicai.cn',
    loginName:1,
}
/**/
const mutations = {
    showLoading (state) { //
        state.infoLoading ? state.infoLoading = false : state.infoLoading = true;
    },
    add(state,n){
        state.loginName += n
    },
    reduce(state){
        state.loginName -= 1
    }
}
/**/
const getters = {

}

export default new Vuex.Store({
  state,
  getters,
  mutations,
})



